.place {
  
  &__photo-container {
      position: relative;
      box-sizing: border-box;
      width: 100%;
      max-height: 256px;
      overflow: hidden;

    &::after {
      content: "";  
      display: block; 
      padding-bottom: 100%;
    }
    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }

  }  
    
  &__photo {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;
    transition: ease-in-out 0.3s;

    &:hover {
      transform: scale(1.1);
    }

  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__description {
    font-size: 16px;
    line-height: 27px;
  }

  &__link {
    font-weight: bold  ;
    font-size: 16px;
    line-height: 27px;
    color: $ColorBlue;
    text-decoration: none;
  }
}